<template>
  <div class="home">
    <h6 id="welcome">Welcome to</h6>
    <h1 id="heading">Bharat Bajaao!</h1>
    <p id="message">India's first dedicated music e-learning platform. In your language in our way. Select an instrument on the left and get started....</p>
    <p>OR</p>
    <div id="uploadcover">
      <input type="file" name="cover" id="cover" class="inputfile">
      <label for="cover" id="cover"><icon name="upload" class="fa" scale="2" aria-hidden="true"></icon><br>Upload your own cover</label>
    </div>
    <div id="eventsbar">
      <div id="eventshead">
        <icon name="calendar" class="fa" scale="3" aria-hidden="true"></icon>
        <h3>Music events across India</h3>
      </div>
      <div class="or-spacer">
        <div class="mask"></div>
      </div>
      :( Sorry, no events available in the next three months. Come back soon.
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      msg: 'Choose the instrument you like to learn'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.home {
  color: #fff;
  text-align: center;
  font-family: "Raleway";
}

#welcome {
  font-size: 20px;
}

#heading {
  font-size: 80px;
}

#message {
  font-size: 25px;
}

#uploadcover {
  background-color: #f44336;
  box-shadow: 1px 1px 2px #000;
  border-radius: 100%;
  height: 120px;
  margin: auto;
  width: 120px;
}

#uploadcover:hover {
  box-shadow: 1px 1px 5px #000;
}

#eventsbar {
  border: 1px solid #fff;
  margin: auto;
  margin-top: 36px;
  width: 80%;
}

#eventshead {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.inputfile {
  width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}

.inputfile + label {
    font-size: 1.25em;
    font-weight: 700;
    color: white;
    display: inline-block;
}

.inputfile:focus + label,
.inputfile + label:hover {
    
}

.inputfile + label {
	cursor: pointer; /* "hand" cursor */
}

.fa {
  color: #fff;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.or-spacer {
  width: 80%;
  position: relative;
  margin-left: 10%;
}
.or-spacer .mask {
  overflow: hidden;
  height: 20px;
}
.or-spacer .mask:after {
  content: '';
  display: block;
  margin: -25px auto 0;
  width: 100%;
  height: 25px;
  border-radius: 125px / 12px;
  box-shadow: 0 0 8px black;
}


</style>



